import Axios from 'axios';
import * as config from '../config.json';
import moment from "moment";

const axios = Axios.create({
  baseURL: config.youtubeApi.endpoint
});

export class YoutubeService {
  getTrendingVideos(countryCode) {
    const params = {
      part: 'snippet',
      chart: 'mostPopular',
      regionCode: countryCode, // should be replaced with country code from countryList
      maxResults: '24',
      key: config.youtubeApi.key
    };


    return axios.get('/', {params});
    /*return axios.get('/', {params}).then(function(res){
      result = res.data.items;
      for (let i = 0; i < result.length; i++) {
        result[i] = {
          id: result[i].id,
          title: result[i].snippet.title,
          thumbnail: result[i].snippet.thumbnails.high.url,
          publishedAt: moment(result[i].snippet.publishedAt).fromNow()
        };
        result[i] = YoutubeService.getVideoDetails(result[i]);
      }

      return result;
    });*/

  }

  formatVideoItems(trendingVideos) {
    const trendingVideosInfo = [];
    for (let i = 0; i < trendingVideos.length; i++) {
      const videoInfo = {
        id: trendingVideos[i].id,
        title: trendingVideos[i].snippet.title,
        thumbnail: trendingVideos[i].snippet.thumbnails.high.url,
        publishedAt: moment(trendingVideos[i].snippet.publishedAt).fromNow()
      };
      trendingVideosInfo.push(videoInfo);      
    }
    return trendingVideosInfo;
  }

  static getVideoDetails(video) {
    const params = {
      part: 'statistics',
      id: video.id,
      key: config.youtubeApi.key
    };

    return axios.get('/', {params}).then(function(res) {
      const result = res.data;
      video.viewCount = result['items'][0].statistics.viewCount;
      video.likeCount = result['items'][0].statistics.likeCount;

      return video;
    });
  }
}
